@charset "UTF-8";

//页面常用icon

%btn{
	display: inline-block;
	vertical-align: middle;
	font-style: normal;
}

.btn{
	@extend %btn;
	height:40px;
	line-height: 36px;
	border-radius: 6px;
	text-align: center;
	cursor: pointer;
	&.block{
		display: block;
	}
}



