@charset "UTF-8";

//页面常用icon

%icon {
    display: inline-block;
    vertical-align: middle;
    font-style: normal;
}

// 箭头
.icon-prev, .icon-next {
    width: 30px;
    height: 30px;
    display: inline-block;
    position: relative;
    // background-color: green;
    &::before {
        content: '\20';
        width: 10px;
        height: 10px;
        border: solid #000;
        border-width: 0 0 1px 1px;
        position: absolute;
        transform: rotate(45deg) translate(6px, 8px);
    }
}

.icon-next {
    &::before {
        transform: rotate(-135deg) translate(-2px, -12px);
    }
}

//心形
.icon-love {
    @extend %icon;
    width: .32rem;
    height: .28rem;
    background: url('../images/icon-love.png') 0 0 no-repeat;
    -webkit-background-size: 100%;
    background-size: 100%;
}

